@import '_vars'

@tailwind base
@tailwind components
@tailwind utilities

html
    scroll-behavior: smooth
    
*
    box-sizing: border-box
    margin: 0
    padding: 0
    outline: none
    border: none
    background: none
    -webkit-tap-highlight-color: transparent
    -webkit-font-smoothing: antialiased
    -moz-osx-font-smoothing: grayscale
